.contact-edit {
    padding-top: 1rem;
    form {
        width: 300px;
        @include flex(column, flex-start, flex-start);
        gap: 0.5rem;

        input {
            outline: none;
            border: none;
            box-shadow: 0px 1px 9px 0px #00000040;
            height: 30px;
            width: 100%;
            padding: 10px;
            font-size: 1rem;
            background-color: $clr2;
            color: $clr5;
        }
    
        ::placeholder {
            color: $clr5;
            opacity: 1;
        }
    
        :-ms-input-placeholder {
            color: $clr5;
        }
    
        ::-ms-input-placeholder {
            color: $clr5;
        }

        button {
            @include btn;
            width: 50%;
            align-self: center;
        }
    }

    .goback {
        @include btn;
    }
    .delete-btn {
        @include btn($clr3, $clr1 );
        &:hover {
            color: red;
        }
    }
    .btn-container {
        width: 300px;
        @include flex(row, center, space-between);
        margin-top: 0.5rem;
    }
}
