.contact-filter {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    input {
        outline: none;
        border: none;
        box-shadow: 0px 1px 9px 0px #00000040;
        height: 30px;
        width: 250px;
        padding: 10px;
        font-size: 1rem;
        background-color: $clr2;
        color: $clr5;
    }

    ::placeholder {
        color: $clr5;
        opacity: 1;
    }

    :-ms-input-placeholder {
        color: $clr5;
    }

    ::-ms-input-placeholder {
        color: $clr5;
    }
}
