* {
    box-sizing: border-box;
}
body {
    margin: 0;
    color: $clr2;
    font-family: domine;
    background-color: $clr5;
}

button,
input,
textarea {
    font-family: domine;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    margin-bottom: 1rem;
}

ul,
li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}
